import { useFormContext } from 'react-hook-form';
import { FormField, FormItem, FormLabel, FormControl, FormMessage } from '@/components/ui/form';
import { Popover, PopoverContent, PopoverTrigger } from '../ui/popover';
import { Button } from '../ui/button';
import { format } from 'date-fns';
import CalendarIcon from '../icons/CalendarIcon';
import { Calendar } from '../ui/calendar';
import { cn } from '@/lib/utils';

interface Props {
    className?: string;
    name: string;
    label?: string;
    placeholder?: string;
    disabled?: (date: Date) => boolean;
}

export default function DayPickerInputForm({ className, name, label, placeholder, disabled = undefined }: Props) {
    const { control } = useFormContext();
    return (
        <FormField
            control={control}
            name={name}
            render={({ field }) => (
                <FormItem className={cn('flex flex-col', className)}>
                    <FormLabel className="text-primery-blue-900 font-medium text-[18px] leading-[33.73px] tracking-normal text-start">
                        {label}
                    </FormLabel>
                    <Popover>
                        <PopoverTrigger asChild>
                            <FormControl>
                                <Button
                                    variant="outline"
                                    className="pe-3 text-start font-medium flex flex-row justify-between"
                                >
                                    {field.value ? (
                                        format(field.value, 'yyyy-MM-dd')
                                    ) : (
                                        <span className="text-white-blue-600">{placeholder}</span>
                                    )}
                                    <CalendarIcon width="24" height="24" className="text-white-blue-600" />
                                </Button>
                            </FormControl>
                        </PopoverTrigger>
                        <PopoverContent className="w-auto p-0" align="start">
                            <Calendar
                                mode="single"
                                selected={field.value}
                                onSelect={field.onChange}
                                disabled={disabled}
                                initialFocus
                            />
                        </PopoverContent>
                    </Popover>
                    <FormMessage />
                </FormItem>
            )}
        />
    );
}
